html {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  box-sizing: border-box;
  background-color: #fff;
  justify-content: space-between;
  padding-left: 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
  margin-top: 10px;
}

.nav {
  align-items: center;
  margin-right: 50px;
  display: flex;
}

.nav__link {
  color: #000;
  align-items: center;
  height: 60px;
  margin: 0;
  padding: 0;
  font-size: 12px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__item {
  text-transform: uppercase;
  color: #000;
  text-align: center;
  margin-left: 20px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  list-style-type: none;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__list {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__link.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
